@import '../../style/style.scss';

.technologies {
  background-color: #f7f4f4;
  &__inner {
    padding: 150px 0;
    text-align: center;
  }
  &__title {
    @extend %extend-title;
    font-family: 'DM Sans', sans-serif;
  }
  &__sub-title {
    @extend %extend-sub-title;
    font-family: 'DM Sans', sans-serif;
  }
  &__list {
    color: var(--dark-content, #666);
    font-size: 18px;
   display: flex;
   justify-content: space-evenly;
   span{
    margin-bottom: 10px;
   }
  }
  &__list-wrapper {
   
  }
  &__list-item {
   display: flex;
   flex-direction: column
    
  }
  &__list-title {
    @extend %extend-sub-title;
    font-size: 18px;
    margin-bottom: 20px;
    color: var(--dark-content, #414141);
    font-family: 'DM Sans', sans-serif;
  }
}

@media (max-width: 650px) {
  .technologies__list-wrapper {
    grid-template-columns: 1fr;
  }
  .technologies__list {
    width: 200px;
    margin-bottom: 30px;
  }
}

@media (max-width: 700px) {
  .technologies__inner {
    padding: 100px 0;
  }
  .technologies__sub-title {
    margin-bottom: 50px;
  }
}
